.sidebar {
  margin-right: 50px;
  width: 22%;
  flex-shrink: 0;

  h2 {
    font-size: 1.8rem;
    margin-bottom: 40px;
    margin-top: 0;
  }

  .sidebar-hotels {
    .s-hotel {
      display: flex;
      margin-bottom: 40px;
      justify-content: space-between;

      .s-photo {
        width: 100px;
        height: 70px;
        border-radius: 5px;
        margin-right: 15px;
        background-size: 100%;
        background-repeat: no-repeat;
        
      }

      .s-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-basis: 200px;

        .s-title {
          font-weight: bold;
          margin-bottom: 5px;
        }

        .s-location {
          color: #999;
          font-size: 0.9rem;
          margin-bottom: 5px;
        }

        .s-price {
          color: #e02c86;
          background-color: rgba(#e02c86, 0.1);
          font-weight: bold;
          padding: 3px 5px;
          width: 50px;
          font-size: 0.9rem;
          border-radius: 15px;
          text-align: center;
        }
      }

      .s-button {
        width: 32px;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        transition: all 0.2s ease;
        cursor: pointer;
        align-self: center;

        &:hover {
          background-color: rgba(#333333, 0.1);
        }

        .s-dot {
          margin: 0 3px;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: #333;
        }
      }
    }
  }
}
